import React from 'react';
import styles from './Projects.module.css';
import Project from "./Project/Project";
import Tittle from "../Common/Title";
import urlChat from "../Common/Assets/chatBackground.jpg";
import urlToDoList from "../Common/Assets/toDolist.jpg";
import urlSN from "../Common/Assets/SN.jpg";
import urlMailer from "../Common/Assets/mailer.jpg";
import Fade from 'react-reveal/Fade';


function Projects() {

    const backGroundImgChat = {
        backgroundImage: `url(${urlChat})`,
        backgroundSize: '70%',
        backgroundPosition: 'center'
    };
    const backGroundImgToDoList = {
        backgroundImage: `url(${urlToDoList})`,
        backgroundSize: '70%',
        backgroundPosition: 'center'
    };
    const backGroundImgSn = {
        backgroundImage: `url(${urlSN})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center'
    };
    const backGroundImgMailer = {
        backgroundImage: `url(${urlMailer})`,
        backgroundSize: '85%',
        backgroundPosition: 'center'
    };

    const toDoListURL = 'https://jeremywylde.github.io/todolist';
    const chatURL = 'https://github.com/JeremyWylde/chat';
    const snURL = 'https://github.com/JeremyWylde/SN';
    const mailerURL = 'https://github.com/JeremyWylde/mailer';

    return (
        <div id='Projects' className={styles.projectsBlock}>
            <div className={styles.container}>
                <Fade right>
                    <Tittle title={'Проекты'}/>
                </Fade>
                <div className={styles.projects}>
                    <Fade right>
                        <Project title={'Список дел'} link={toDoListURL}
                                 description={'Список дел выполнен при помощи стака технологий: React + Redux и ' +
                                 'CSS фреймворка Bulma. В качестве API используется ' +
                                 'https://social-network.samuraijs.com'}
                                 backgroundImg={backGroundImgToDoList}/>
                    </Fade>

                    <Fade right>
                        <Project title={'Чат'} link={chatURL}
                                 description={'Для написания чата использовался react, bootstrap, node.js, express ' +
                                 'и socket.io'}
                                 backgroundImg={backGroundImgChat}/>
                    </Fade>
                    <Fade right>
                        <Project title={'Социальная сеть'} link={snURL}
                                 description={'Социальная сеть выполнена при помощи React, Redux. Используются библиотеки:' +
                                 'Redux form, Redux thunk, Axios. В качестве api используется https://social-network.samuraijs.com' +
                                 ''}
                                 backgroundImg={backGroundImgSn}/>
                    </Fade>
                    <Fade right>
                        <Project title={'Приложение для отправки писем'} link={mailerURL}
                                 description={'Простое back-end приложение, для отправки данных из формы обратной связи на email.' +
                                 'Создано при помощи Node.js и Express'}
                                 backgroundImg={backGroundImgMailer}/>
                    </Fade>
                </div>
            </div>
        </div>
    );
}

export default Projects;
