import React from 'react';
import styles from './Btn.module.css';

function Btn(props) {
    return <>
        {props.link ? <div className={styles.btnYo}><a href={props.link}>{props.title}</a></div>
        : <button className={styles.btnYo} onClick={props.action} style={props.color}>{props.title}</button>}
        </>
}

export default Btn;