import React, {useState} from 'react';
import styles from './Contacts.module.css';
import Tittle from "../Common/Title";
import Fade from 'react-reveal/Fade';
import Btn from "../Common/Btn";
import axios from "axios";

function Contacts() {
    const [isFormFieldOut, setIsFormFieldOut] = useState(false);
    let [name, setName] = useState('');
    let [email, setEmail] = useState('');
    let [message, setMessage] = useState('');

    const onSubmit = (event) => {
        event.preventDefault();
        axios.post('https://jeremywyldemailerapp.herokuapp.com/sentmessage',
            `name=${name}&email=${email}&message=${message}`)
            .then(()=>{
                    setIsFormFieldOut(true);
                    setTimeout(()=>{setIsFormFieldOut(false)},5000);
            },
                ()=>{
                    setIsFormFieldOut(true);
                    setTimeout(()=>{setIsFormFieldOut(false)},5000);
                    setName('');
                    setEmail('');
                    setMessage('');
                })

    };

    const onNameChange = (e) =>{
        setName(e.target.value);
    };
    const onEmailChange = (e) =>{
        setEmail(e.target.value);
    };
    const onMessageChange = (e) =>{
        setMessage(e.target.value);
    };


    return (
        <div id={'contacts'} className={styles.contactsBlock}>
            <div className={styles.container}>
                {isFormFieldOut ? <h2 className={styles.alert}>Сообщение отправлено!</h2> :
                    <Form handleSubmit={onSubmit} onNameChange={onNameChange} onEmailChange={onEmailChange}
                          onMessageChange={onMessageChange} nameValue={name} emailValue={email}
                          messageValue={message}/>}
            </div>
        </div>
    );
}


//https://jeremywyldemailerapp.herokuapp.com/sentmessage
function Form(props) {
    const color = {
        color: `#ffffff`
    };
    return (
        <div className={styles.container}>
            <Fade bottom>
                <Tittle title={'Контакты'}/>
                <form className={styles.form} onSubmit={props.handleSubmit}>
                    <input className={styles.formArea} type="text" name="name" placeholder='Введите свое имя'
                           onChange={props.onNameChange} value={props.nameValue}/>
                    <input className={styles.formArea} type="email" name="email" placeholder='Ввдите свой email'
                           onChange={props.onEmailChange} value={props.emailValue}/>
                    <textarea className={styles.messageArea} name="message" placeholder='Введите сообщение'
                              onChange={props.onMessageChange} value={props.messageValue}/>
                    <Btn title={'Отправить'} color={color}/>
                </form>
                <p></p>
            </Fade>
        </div>
    )
}


export default Contacts;
