import React from 'react';
import styles from './Skills.module.css';
import Skill from "./Skill/Skill";
import Tittle from "../Common/Title";
import reactIcon from '../Common/Assets/React.svg';
import js from '../Common/Assets/JS.svg';
import htmlCss from '../Common/Assets/HTML&CSS.svg';
import ReduxIcon from '../Common/Assets/Redux.svg';
import NodeIcon from '../Common/Assets/Node.js.svg';
import Fade from 'react-reveal/Fade';

function Skills() {
    return (
        <div id={'Skills'} className={styles.skillsBlock}>
            <div className={styles.skillsContainer}>
                <Fade left>
                <Tittle title={'Мои скилы'}/>
                </Fade>
                <div className={styles.skills}>
                    <Fade left>
                    <Skill tittle={'Js'} icon={js}
                           description={'Базовое знание JavaScript(ES6) '}/>
                    </Fade>
                    <Fade left>
                    <Skill tittle={'HTML & Css'} icon={htmlCss}
                           description={'Навыки адаптивной верстки '}/>
                </Fade>
                    <Fade left>
                    <Skill tittle={'React'} icon={reactIcon}
                           description={'Использовал в проектах: функциональные и классовые компоненты, методы жизненного цикла, хуки, ' +
                           ' хоки, контекст, рефы  и проверку типов данных с помощью PropTypes  '}/>
                    </Fade>
                    <Fade left>
                    <Skill tittle={'Redux'} icon={ReduxIcon}
                           description={'Знаком(и использовал в проектах) с основными понятиями Redux -  Store, Actions, Reducers и Thunk.' +
                           ' Для более глубокого понимания реализовывал свой собственный стор, в проекте "социальная сеть"'}/>
                    </Fade>
                    <Fade left>
                    <Skill tittle={'Node.Js'} icon={NodeIcon}
                           description={'Начальные знания Node.js, фреймворка Express и socket.io'}/>
                    </Fade>
                </div>
            </div>
        </div>
    );
}

export default Skills;
