import React from 'react';
import styles from './Footer.module.css';
import telegramIcon from '../Common/Assets/telegram.svg'
import vkIcon from '../Common/Assets/vk-1.svg'
import gitHubIcon from '../Common/Assets/github.svg'
import Fade from 'react-reveal/Fade';



function Footer() {
    return (
        <div className={styles.footerBlock}>
            <div className={styles.container}>
                <div className={styles.icons}>
                    <Fade>
                    <div className={styles.icon}>
                        <a href='https://t.me/JeremyWylde'>
                            <img src={telegramIcon}/>
                        </a>
                    </div>
                    <div className={styles.icon}>
                        <a href='https://vk.com/id93481900'>
                            <img src={vkIcon}/>
                        </a>
                    </div>
                    <div className={styles.icon}>
                        <a href='https://github.com/JeremyWylde'>
                            <img src={gitHubIcon}/>
                        </a>
                    </div>
                    </Fade>
                </div>
                <p></p>
                <div className={styles.rec}>© Все права защищены</div>
            </div>
        </div>
    );
}

export default Footer;
